<template>
  <div>
    <div class="image-item" v-if="isShow">
      <img :src="image.iimagesrc" alt="">
      <div class="text" @click="delImage" v-show="delShow">×</div>
    </div>
    <div class="image-item" v-else>
      <img :src="images" alt="">
    </div>
  </div>
  
</template>

<script>
  export default {
    name: 'ShowImage',
    props: {
      image: {
        type:Object,
        default() {
          return {}
        }
      },
      images: {
        type: String,
        default: ''
      },
      index: {
        type: Number,
        default: 0
      },
      isShow: {
        type: Boolean,
        default: false
      },
      delShow: {
        type: Boolean,
        default: false
      }
    },
    methods: {
      delImage() {
        //删除图片  发送事件  发送图片的id  和下标  并删除
        this.$emit('delImage', this.index, this.image)
      }
    }
  }
</script>

<style scoped>
  .image-item{
   height: 50px;
   width: 50px; 
   position: relative;
  } 
  .image-item img{
    height: 50px;
    width: 50px;
    
  }
  .text{
    background-color: red;
    height: 15px;
    width: 15px;
    font-size: 13px;
    line-height: 15px;
    text-align: center;
    position: absolute;
    right: 2px;
    top: 2px;
    border-radius: 10px;
  }
</style>