<template>
  <div class="manager">
    <nav-bar class="nav-barl">
      <div slot="left" class="nav-left" @click="backClick">
        <img src="~assets/img/back.svg" alt="">
      </div>
      <div slot="center" class="nav-titles">商品管理</div>
    </nav-bar>
    <!--搜索栏-->
    <manager-search @searchGoods="searchGoods" @addGoods="addGoods" > </manager-search>
    <scroll class="content" ref="scroll" >
      <div class="getgoods" v-if="isActive">
        <!--展示商品的数据-->
        <div v-if="isHaveGoods">
          <!--查看商品-->
          <manager-show-good v-if="isShowGood" :good="manGood" :goodsImg="goodsImg" @updPage="updPage" @delGood="delGood"></manager-show-good>
          <!--修改商品-->
          <manager-upd-good v-else :good="manGood" :goodsImg="goodsImg" @pageChange="pageChange" @formSub="formSub"></manager-upd-good>
        </div>
        <!--当没有商品时-->
        <div v-else class="isnull">暂无商品</div>
      </div>
      <!--增加商品-->
      <div class="addgoods" v-else>
        <manager-add-good @addSub="addSub"></manager-add-good>
      </div>

    </scroll>
  </div>
</template>

<script>
  import Scroll from 'common/scroll/Scroll'
    import NavBar from 'common/navbar/NavBar'
  import ManagerSearch from './childcomps/ManagerSearch'
  import ManagerShowGood from './childcomps/ManagerShowGood'
  import ManagerAddGood from './childcomps/ManagerAddGood'
  import ManagerUpdGood from './childcomps/ManagerUpdGood'
  import {getDetails} from 'network/detail'
  import {delGoods, selMaxId} from 'network/goods'
  export default {
    name: 'Manager',
    components: {
      Scroll,
      NavBar,
      ManagerSearch,
      ManagerShowGood,
      ManagerAddGood,
      ManagerUpdGood
    },
    data () {
      return {
        //保存商品数据
        manGood: null,
        goodsImg: null,
        //判断显示查找商品还是添加商品
        isActive: true,
        //判断显示查找商品还是修改商品
        isShowGood: true
      }
    },
    computed: {
      //判断显示那个信息
      isHaveGoods() {
        if(this.manGood == null) return false
        return true
      }
    },
    methods: {
      backClick() {
        this.$router.back()
      },
      //查找商品
      searchGoods(gid) {
        //先将页面切换到查找商品页面
        this.isActive = true
        this.isShowGood = true
        //判断id是否存在
        if(gid == '') {
          this.$toast.show("请输入商品的id")
        } else if(typeof gid == Number) {
          this.$toast.show("请输入正确商品的id")
        } else {
          //发送请求获取数据  并赋值
          getDetails(gid).then(res => {
            if(res != null) {
              this.manGood = res[0];
              this.goodsImg = res[1]
            } else {
              this.$toast.show("该商品不存在")
            }
            
          })
        }
      },
      formSub(gid) {
        //提交表单
        //重新请求商品信息  显示商品展示页面
        //发送请求获取数据  并赋值
          getDetails(gid).then(res => {
            if(res != null) {
              this.manGood = res[0];
              this.goodsImg = res[1];
               this.$toast.show("修改成功")
            } else {
              this.$toast.show("该商品不存在")
            }
            
          })
        this.isShowGood = true;
      },
      updPage() {
        //跳转到修改商品页面
        this.isShowGood = false
      },
      delGood() {
        //删除商品   发送请求  让数组未空  gid也为空
        delGoods(this.manGood.gid).then(res => {
            this.$toast.show(res)
            this.manGood = null         
        })
        

      },
      pageChange() {
          //展示商品页面
          this.isShowGood = true
      },
      //添加商品
      addGoods() {
        //修改显示的页面
        this.isActive = false
      },
      addSub() {
        //获取添加商品的id  请求商品的信息  显示商品展示页面
        selMaxId().then(res => {
           getDetails(res).then(res => {
            if(res != null) {
              this.manGood = res[0];
              this.goodsImg = res[1];
               this.$toast.show("添加成功")
               this.isActive = true;
               this.isShowGood = true;

            } else {

            }
            
          })
        })
      }
    }
  }
</script>

<style scoped>
  .manager{
    height: 100vh;
  }
  .nav-barl{
    background-color: #ff8198;
    color: white;
  }
  .nav-left{
    margin-top: 5px;
  }
  .isnull{
    text-align: center;
    margin:auto;
    margin-top: 30px;
  }
  .content{
    height: calc(100% - 93px - 44px);
    overflow: hidden;
  }
</style>