import {request} from './request'




//修改商品  images  删除
export function updGoodsImg(iid) {
  return request({
    url: 'updGoodsImg.do',
    method: 'post',
    data: {
      iid
    }
  })
}
//修改商品 goods
export function updGoodsImgByGid(gid, iimagesrc) {
  return request({
    url: 'updGoodsImgByGid.do',
    method: 'post',
    data: {
      gid,iimagesrc
    }
  })
}


//删除商品
export function delGoods(gid) {
  return request({
    url: 'delGoods.do',
    method: 'post',
    data: {
      gid
    }
  })
}

//添加商品   
//获取商品中最大的id
export function selMaxId() {
  return request({
    url: 'selMaxId.do',
    method: 'post'
  })
}


