<template>
  <div class="search">
    <!--搜索栏-->
      <div >
      <input  class="gid" type="text" v-model="gid" name="uid" placeholder="商品id"> 
      </div>
      <div class="sea" @click="searchGoods">查找商品</div>
      <div class="sea" @click="addGoods">增加商品</div>
  </div>
</template>

<script>
  export default {
    name: 'ManagerSearch',
    data () {
      return {
        gid: ''
      }
    },
    methods: {
      searchGoods() {
        //点击查找  将gid的值发送给父组件
        this.$emit('searchGoods', this.gid)
      },
      addGoods() {
        this.$emit('addGoods')
      }
    }
  }
</script>

<style scoped>
  .search{
    width: 100%;
    display: flex;
    justify-content: space-around;
    font-size: 13px;
    height: 44px;
    border-bottom: 2px solid #ff8198;
  }
  .gid{
    width: 50px;
    margin-top: 12px;
    height: 20px;
    display: block;
    border-radius: 10px;
  }
  .sea{
    border-radius: 5px;
    height: 25px;
    margin-top: 12px;
    width: 60px;
    font-size: 12px;
    background-color: forestgreen;
    line-height: 25px;
    text-align: center;
  }
</style>