<template>
  <div>
    <form action="http://localhost:8088/cakemall/updGoods.do" enctype="multipart/form-data" method="post" target="ifrs" class="ff" >
        <input type="text" name="gid" v-model="getGood.gid" v-show="false"/>
         <table>
          <tr>
            <td>蛋糕的标题：</td>
            <td><input type="text" name="gtitle" v-model="getGood.gtitle"/></td>
          </tr>
          <tr>
            <td>蛋糕的种类：</td>
            <td>
              <select name="gkind" v-model="getGood.gkind">
                <option v-for="(item, index) of categories" :key="index" :value="item.name">{{item.title}}</option>
              </select>
            </td>
          </tr>
          <tr>
            <td>6寸的价格：</td>
            <td>￥<input type="text" name="gsize6" v-model="getGood.gsize6"/></td>
          </tr>
          <tr>
            <td>8寸的价格：</td>
            <td>￥<input type="text" name="gsize8" v-model="getGood.gsize8"/></td>
          </tr>
          <tr>
            <td>10寸的价格：</td>
            <td>￥<input type="text" name="gsize10" v-model="getGood.gsize10"/></td>
          </tr>
          <tr>
            <td>12寸的价格：</td>
            <td>￥<input type="text" name="gsize12" v-model="getGood.gsize12"/></td>
          </tr>
          <tr>
            <td>14寸的价格：</td>
            <td>￥<input type="text" name="gsize14" v-model="getGood.gsize14"/></td>
          </tr>
          <tr>
            <td>16寸的价格：</td>
            <td>￥<input type="text" name="gsize16" v-model="getGood.gsize16"/></td>
          </tr>
          <tr>
            <td>18寸的价格：</td>
            <td>￥<input type="text" name="gsize18" v-model="getGood.gsize18"/></td>
          </tr>
          <tr>
            <td>20寸的价格：</td>
            <td>￥<input type="text" name="gsize20" v-model="getGood.gsize20"/></td>
          </tr>
          <tr>
            <td>蛋糕的收藏数：</td>
            <td>{{good.gcollect}}</td>
          </tr>
          <tr>
            <td>蛋糕的销量：</td>
            <td>{{good.gsale}}</td>
          </tr>
          <tr>
            <td>蛋糕的产地：</td>
            <td><input type="text" name="gplace" v-model="getGood.gplace"/></td>
          </tr>
          <tr>
            <td>蛋糕的保质期：</td>
            <td><input type="text" name="glife" v-model="getGood.glife"/></td>
          </tr>
          <tr>
            <td class="name">上传图片：</td>
            <td>
              <input class="fimg" type="file" name="files" id="allf"  multiple="multiple" @change="fileChange"/>
            </td>
          </tr>
          <tr>
            <td>蛋糕的图片：</td>
            <td class="images">
              <!--展示图片信息  先展示已存在的图片 在展示为存在的图片-->
              <show-image v-for="(item, index) of gimg" :key="item.iimagesrc" :image="item" :index="index" :isShow="true" :delShow="true" @delImage="delImage"></show-image>
              <!--上传文件-->
              <show-image v-for="(item, index) of updImg" :key="index" :images="item"></show-image>
            </td>
          </tr>
         </table>
         <div class="but">
           <input type="submit" value="确定" class="fbut" @click="doImg"/>
           <div class="back" @click="back">取消</div>
         </div>
        
      </form>
      <iframe name="ifrs" id="ifrs" @load="ifload" ref="iframe" v-show="false"></iframe>
  </div>
</template>

<script>
  import ShowImage from './ShowImage'
  import {updGoodsImg, updGoodsImgByGid} from 'network/goods'
  export default {
    name: 'ManagerUpdGood',
    components: {
      ShowImage
    },
    props: {
      good: {
        type: Object,
        default() {
          return {}
        }
      },
      goodsImg: {
        type: Array,
        default() {
          return []
        }
      }
    },
    data () {
      return {
         categories: [
          {
            title: '新品',
            name: 'news'
          },
          {
            title: '流行',
            name: 'pop'
          },
          {
            title: '精选',
            name: 'sell'
          },
          {
            title: '生日',
            name: 'birth'
          },
          {
            title: '寿糕',
            name: 'older'
          },
          {
            title: '卡通',
            name: 'katong'
          },
          {
            title: '水果',
            name: 'friut'
          },
          {
            title: '儿童',
            name: 'child'
          },
          {
            title: '植脂',
            name: 'zhizhi'
          },
          {
            title: '植脂',
            name: 'ruzhi'
          }
        ],
        //保存父组件传递的商品信息
        getGood: JSON.parse(JSON.stringify(this.good)),
        //保存商品的图片
        gimg: JSON.parse(JSON.stringify(this.goodsImg)),
        //保存新增商品的图片
        updImg: [],
        //保存要删除的图片id
        delImg: [],
        first: null
      }
    },
    methods: {
      ifload() {
        //页面加载时也会调用一次
        if(this.first == null) {
          this.first = 1
        }else {
          //让页面显示为展示商品
          this.$emit('formSub', this.getGood.gid)
        }
      },
      fileChange() {
        //当选择文件时修改展示图片的数组
        //每次改变时图片数组都要重新赋值
        this.updImg = []
        var file =document.getElementById("allf");
        for(let i = 0; i < file.files.length; i++) {
          this.updImg[i] =window.URL.createObjectURL(file.files[i]);
        }
      },
      
      delImage(index, image) {
        //删除图片  
        this.gimg.splice(index, 1)
        //将图片的下标保存到数组中
        this.delImg.push(image)

      },
     
      doImg() {
        let check = false;
        //先判断是否满足提交需求  满足条件且删除的图片为0
        if(true) {
          //在提交from表单的同时 将要删除的图片对象 数组上传提交到后台
          //删除了图片
          if(this.delImg.length != 0) {
            for(let item of this.delImg) {
              if(item.iid == 0) {
                check = true;//删除了goods中的图片 就最后修改其中的图片
              }else {
                //直接删除其他的图片
                updGoodsImg(item.iid).then(res => {
                  if(res == 0) {
                    //删除失败
                    this.$toast.show("删除失败")
                  }
                })
              }
              if(check) {//需要修改goods中的图片路径
                //判断gimg中的数组长度
                if(this.gimg.length == 0) {
                  //都删除了 就发送空的图片路径
                  updGoodsImgByGid(item.gid, '').then(res => {
                    if(res == 0) {
                      // this.$toast.show("删除失败")
                    }
                  })
                }else{
                  //获取gimg中的第一个图片 将该图片的路径   最后删除该图片
                   updGoodsImgByGid(item.gid, this.gimg[0].iimagesrc).then(res => {
                    if(res == 0) {
                      // this.$toast.show("修改失败")
                    }else {
                      //删除gimg中的第一张图片
                      updGoodsImg(this.gimg[0].iid).then(res => {
                        if(res == 0) {
                          //删除失败
                          // this.$toast.show("删除失败")
                        }
                      })
                    }
                    
                  })
                }
              }

            }
            
          }
          
        }
        
        
      },
      back() {
        //发送事件 修改当前显示的页面
        this.$emit('pageChange')
      }
    }
  }
</script>

<style scoped>
  .images, .but{
    display: flex;
    justify-content: space-around; 
    flex-wrap: wrap;
  }
  .fbut{
    background-color: forestgreen
  }
  .back{
    background-color: red;
    width: 40px;
    line-height: 25px;
    text-align: center;
  }
  .fimg{
    width: 180px;
  }
  .back, .fbut{
    border-radius: 5px;
    margin-top: 10px;
  }
  
</style>