<template>
  <div>
    <form action="http://localhost:8088/cakemall/addGood.do" enctype="multipart/form-data" method="post" target="ifrss" class="ff" >
         <table>
          <tr>
            <td>蛋糕的标题：</td>
            <td><input type="text" name="gtitle" /></td>
          </tr>
          <tr>
            <td>蛋糕的种类：</td>
            <td>
              <select name="gkind">
                <option v-for="(item, index) of categories" :key="index" :value="item.name">{{item.title}}</option>
              </select>
            </td>
          </tr>
          <tr>
            <td>6寸的价格：</td>
            <td>￥<input type="text" name="gsize6" /></td>
          </tr>
          <tr>
            <td>8寸的价格：</td>
            <td>￥<input type="text" name="gsize8" /></td>
          </tr>
          <tr>
            <td>10寸的价格：</td>
            <td>￥<input type="text" name="gsize10" /></td>
          </tr>
          <tr>
            <td>12寸的价格：</td>
            <td>￥<input type="text" name="gsize12" /></td>
          </tr>
          <tr>
            <td>14寸的价格：</td>
            <td>￥<input type="text" name="gsize14"/></td>
          </tr>
          <tr>
            <td>16寸的价格：</td>
            <td>￥<input type="text" name="gsize16" /></td>
          </tr>
          <tr>
            <td>18寸的价格：</td>
            <td>￥<input type="text" name="gsize18"/></td>
          </tr>
          <tr>
            <td>20寸的价格：</td>
            <td>￥<input type="text" name="gsize20" /></td>
          </tr>
          <tr>
            <td>蛋糕的产地：</td>
            <td><input type="text" name="gplace" /></td>
          </tr>
          <tr>
            <td>蛋糕的保质期：</td>
            <td><input type="text" name="glife" /></td>
          </tr>
          <tr>
            <td class="name">上传图片：</td>
            <td>
              <input class="fimg" type="file" name="files" id="allfs"  multiple="multiple" @change="fileChange"/>
            </td>
          </tr>
          <tr>
            <td>蛋糕的图片：</td>
            <td class="images">
              <!--上传文件-->
              <show-image v-for="(item, index) of updImg" :key="index" :images="item"></show-image>
            </td>
          </tr>
         </table>
         <div class="but">
           <input type="submit" value="添加商品" class="fbut"/>
         </div>
        
      </form>
      <iframe name="ifrss" id="ifrss" @load="ifload" ref="iframe" v-show="false"></iframe>
  </div>
</template>

<script>
import ShowImage from './ShowImage'
  export default {
     
    name: 'ManagerAddGood',
    components: {
      ShowImage
    },
    data () {
      return {
         categories: [
          {
            title: '新品',
            name: 'news'
          },
          {
            title: '流行',
            name: 'pop'
          },
          {
            title: '精选',
            name: 'sell'
          },
          {
            title: '生日',
            name: 'birth'
          },
          {
            title: '寿糕',
            name: 'older'
          },
          {
            title: '卡通',
            name: 'katong'
          },
          {
            title: '水果',
            name: 'friut'
          },
          {
            title: '儿童',
            name: 'child'
          },
          {
            title: '植脂',
            name: 'zhizhi'
          },
          {
            title: '植脂',
            name: 'ruzhi'
          }
        ],
        //保存新增商品的图片
        updImg: []
      }
    },
    methods: {
      ifload() {
        //页面加载时也会调用一次
        if(this.first == null) {
          this.first = 1
        }else {
          //让页面显示为展示商品
          this.$emit('addSub')
        }
      },
       fileChange() {
        //当选择文件时修改展示图片的数组
        //每次改变时图片数组都要重新赋值
        this.updImg = []
        var file =document.getElementById("allfs");
        for(let i = 0; i < file.files.length; i++) {
          this.updImg[i] =window.URL.createObjectURL(file.files[i]);
        }
      },
    }
  }
</script>

<style scoped>
  .images, .but{
    display: flex;
    justify-content: space-around; 
    flex-wrap: wrap;
  }
  .fbut{
    background-color: forestgreen
  }
  .fimg{
    width: 180px;
  }
  .fbut{
    border-radius: 5px;
    margin-top: 10px;
  }
</style>