<template>
  <div>
    <table>
        <tr>
          <td>蛋糕的标题：</td>
          <td>{{good.gtitle}}</td>
        </tr>
        <tr>
          <td>蛋糕的种类：</td>
          <td>{{getGkind}}</td>
        </tr>
        <tr>
          <td>6寸的价格：</td>
          <td>￥{{good.gsize6}}</td>
        </tr>
        <tr>
          <td>8寸的价格：</td>
          <td>￥{{good.gsize8}}</td>
        </tr>
        <tr>
          <td>10寸的价格：</td>
          <td>￥{{good.gsize10}}</td>
        </tr>
        <tr>
          <td>12寸的价格：</td>
          <td>￥{{good.gsize12}}</td>
        </tr>
        <tr>
          <td>14寸的价格：</td>
          <td>￥{{good.gsize14}}</td>
        </tr>
        <tr>
          <td>16寸的价格：</td>
          <td>￥{{good.gsize16}}</td>
        </tr>
        <tr>
          <td>18寸的价格：</td>
          <td>￥{{good.gsize18}}</td>
        </tr>
        <tr>
          <td>20寸的价格：</td>
          <td>￥{{good.gsize20}}</td>
        </tr>
        <tr>
          <td>蛋糕的收藏数：</td>
          <td>{{good.gcollect}}</td>
        </tr>
        <tr>
          <td>蛋糕的销量：</td>
          <td>{{good.gsale}}</td>
        </tr>
        <tr>
          <td>蛋糕的产地：</td>
          <td>{{good.gplace}}</td>
        </tr>
        <tr>
          <td>蛋糕的保质期：</td>
          <td>{{good.glife}}</td>
        </tr>
        <tr>
          <td class="name">蛋糕的图片：</td>
          <td class="showimgs">
            <show-image v-for="(item, index) of goodsImg" :key="index" :image="item" :isShow="true"></show-image>
          </td>
        </tr>
      </table>
      <div class="ud">
        <div class="upd" @click="updGood">修改</div>
      <div class="del" @click="delGood">删除</div>
      </div>
  </div>
</template>

<script>
  import ShowImage from './ShowImage'
  export default {
    name: 'ManagerShowGood',
    components: {
      ShowImage
    },
    props: {
      good: {
        type: Object,
        default() {
          return {}
        }
      },
      goodsImg: {
        type: Array,
        default() {
          return []
        }
      }
    },
    data () {
      return {
         categories: [
          {
            title: '新品',
            name: 'news'
          },
          {
            title: '流行',
            name: 'pop'
          },
          {
            title: '精选',
            name: 'sell'
          },
          {
            title: '生日',
            name: 'birth'
          },
          {
            title: '寿糕',
            name: 'older'
          },
          {
            title: '卡通',
            name: 'katong'
          },
          {
            title: '水果',
            name: 'friut'
          },
          {
            title: '儿童',
            name: 'child'
          },
          {
            title: '植脂',
            name: 'zhizhi'
          },
          {
            title: '植脂',
            name: 'ruzhi'
          }
        ]
      }
    },
    computed: {
      //获取商品的种类
      getGkind() {
        for(let item of this.categories) {
          if(item.name == this.good.gkind) {
            return item.title
          }
        }
      }
    },
    methods: {
      updGood() {
        //跳转到修改页面  发送事件
        this.$emit('updPage')

      },
       delGood() {
        //删除商品   发送事件 
        this.$emit('delGood')
      }
    }
    
  }
</script>

<style scoped>
  .name{
    width: 100px;
  }
  .showimgs, .ud{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  }
  .upd, .del{
    margin-top: 20px;
    border-radius: 10px;
    height: 30px;
    line-height: 30px;
    margin-bottom: 10px;
    width: 50px;
    text-align: center;
  }
  .upd{
    background-color: forestgreen
  }
  .del{
    background-color: red;
    width: 40px;
    line-height: 25px;
    text-align: center;
  }
</style>